import { Component } from '@angular/core';
import { IonicPage, ModalController, NavController, AlertController, Platform, LoadingController } from 'ionic-angular';
import { Contacts, User, Api } from '../../providers';
import { SocialSharing } from '@ionic-native/social-sharing';
import { CallNumber } from '@ionic-native/call-number';

 
@IonicPage()
@Component({
  selector: 'page-list-master',
  templateUrl: 'list-master.html'
})
export class ListMasterPage {
  cardItems: any = [];

  currentItems: any ;
  btnView = false;
  btnModal = false;
  btnCall = false;
  btnEmail = false;
  public loading : any ;

  constructor(public loadingCtrl: LoadingController, private api : Api,private platform: Platform, private alertCtrl: AlertController, private socialSharing: SocialSharing, private callNumber: CallNumber, public user : User, public navCtrl: NavController, public contacts: Contacts, public modalCtrl: ModalController) {
    //console.log(this.contacts.buyer);
    //console.log('con');
    //this.contacts.init(this.user.userInfo);

    this.loading = this.loadingCtrl.create({
      content: `
        <div class="custom-spinner-container" style="background-color: black;">
          <div class="custom-spinner-box" style="background-color: black;"></div>
        </div>`
    });
    this.loading .present();
  }
  ionViewCanEnter() { 
    this.loadContacts();

  }

  initializeItems() {
  //  this.currentItems = this.contacts.buyer;
  }


  getItems(ev: any) {

    this.currentItems = this.cardItems;
    const val = ev.target.value;

    if (val && val.trim() != '') {
      this.currentItems = this.currentItems.filter((item) => {       

        if (item.fullname.toLowerCase().indexOf(val.toLowerCase()) > -1) {
          return true;
        } else if (item.email.toLowerCase().indexOf(val.toLowerCase()) > -1) {
          return true;

        } else if (item.cellphone.toLowerCase().indexOf(val.toLowerCase()) > -1) {
          return true;

        }

      })
    }
  }


  ionViewDidLoad() {

  }

  doRefresh(refresher){


    let response = this.contacts.init(this.user.userInfo);

    Promise.all([response]).then(data => {
      refresher.complete();

    });  
  }

  view(item){

    if (this.btnView)
      return;
    this.btnView = true;


    this.user.userInfo.userid;

    this.contacts.query('m=visit&user='+ this.user.userInfo.userid+'&visitid=' + item + '&token=' + this.user.userInfo.token).subscribe(data => {
      this.contacts.contact = data.json().data;
      this.navCtrl.push('LeadPage');
      this.btnView = false;
    });   

  }

  addItem() {
    // if (this.btnModal)
    //   return;
    // this.btnModal = true;

    this.navCtrl.push('AddleadPage');

    // let addModal = this.modalCtrl.create('AddleadPage');
    
    // addModal.onDidDismiss(item => {
    //   this.btnModal = false;
    // });

    // addModal.present();
  }

  call(number, fullname){

    if (this.btnCall)
      return;
    this.btnCall = true;

    this.callNumber.callNumber(number, true)
      .then(res => this.btnCall = false )
      .catch(err => this.btnCall = false);
 
  }

  mail(email, fullname){

    if (this.btnEmail)
      return;
    this.btnEmail = true;

    this.socialSharing.shareViaEmail('', 'Asunto', [email]).then(() => {

      this.btnEmail = false;
    }).catch(() => {

      this.btnEmail = false;
    }); 






  
  }

 whatsapp(number, fullname){

  

  this.platform.ready().then(() => {

    /** Compartir la propiedad por whatsapp */
    this.socialSharing.shareViaWhatsAppToReceiver(number, '').then(() => {
      // Success!
    }).catch(() => {
      // Error!
    });
  });






}
  /**Abrir pagina de filtros */
  filtre() {
    this.navCtrl.push('FiltrePage', { page: 'buyer' });
  }



  loadContacts() {

    var $this = this;
    this.cardItems = [];

    
    this.contacts.buyer.forEach((value, key, index) => {

      var integer = 0;
      var integer2 = 0;


      //Oficina
      if (this.api.filtreBuyer.officeid) {
        if (!( parseInt(this.api.filtreBuyer.officeid) == parseInt(value.office) )) {
          return;
        }
      }

      $this.cardItems.push(value);
  

    });

    this.currentItems = this.cardItems;
    this.loading.dismiss();

  }


}



